import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { normalizeRawUrl } from '@mssgme/helpers';
import { useActions, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { ImageField, TextField, ToggleField } from '@mssgme/ui';
import { Image, MediaSizes, useUploads } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockEditor } from '../../../components';

export function ImageEdit({ blockId }) {
    const { api } = useActions();
    const { getMediaSizeUrl } = useUploads();
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { bind } = useBlockEditor({ website, page, blockId, as: ImageEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Image.heading')}>
            {({ values, setFieldValue, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <ImageField
                        required
                        name="url"
                        innerLabel={t('Page.Edit.Image.fields.url.label')}
                        previewUrl={getMediaSizeUrl(values.absoluteUrl, MediaSizes.Icon)}
                        upload={(payload) => api.websites.upload({ ...payload, websiteId: website._id })}
                        onChange={({ path, url }) => {
                            setFieldValue('url', path || '');
                            setFieldValue('absoluteUrl', url || '');
                        }}
                        style={{ marginBottom: 12 }}
                    />
                    <TextField
                        builderInput
                        optional
                        name="externalUrl"
                        label={t('Page.Edit.Image.fields.externalUrl.label')}
                        placeholder={t('Page.Edit.Image.fields.externalUrl.placeholder')}
                        maxLength={2047}
                        onBlur={() => {
                            const url = normalizeRawUrl(values.externalUrl || '');

                            if (url && url !== values.externalUrl) {
                                setFieldValue('externalUrl', url);
                            }
                        }}
                    />
                    <ToggleField
                        builderInput
                        label={t('Page.Edit.Image.fields.modal.label')}
                        name="modal"
                        value={values.externalUrl ? false : !!values.modal}
                        disabled={!!values.externalUrl}
                    />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

ImageEdit.propTypes = {
    blockId: PropTypes.string,
};

ImageEdit.block = Image;
