export const EDITORS_MAP = {
    [TextRichEdit.block.kind]: TextRichEdit,
    [TimerEdit.block.kind]: TimerEdit,
    [GiphyEdit.block.kind]: GiphyEdit,
    [CustomCodeEdit.block.kind]: CustomCodeEdit,
    [SubscriptionFormEdit.block.kind]: SubscriptionFormEdit,
    [CustomEdit.block.kind]: CustomEdit,
    [ServicesGridEdit.block.kind]: ServicesGridEdit,
    [ProductsEdit.block.kind]: ProductsEdit,
    [SocialsEdit.block.kind]: SocialsEdit,
    [QuoteEdit.block.kind]: QuoteEdit,
    [ListEdit.block.kind]: ListEdit,
    [AudioEdit.block.kind]: AudioEdit,
    [VideoGalleryEdit.block.kind]: VideoGalleryEdit,
    [FeaturesEdit.block.kind]: FeaturesEdit,
    [AccordionEdit.block.kind]: AccordionEdit,
    [ImageGalleryEdit.block.kind]: ImageGalleryEdit,
    [DividerEdit.block.kind]: DividerEdit,
    [HatEdit.block.kind]: HatEdit,
    [HeadingEdit.block.kind]: HeadingEdit,
    [ImageEdit.block.kind]: ImageEdit,
    [MessengersEdit.block.kind]: MessengersEdit,
    [LinkEdit.block.kind]: LinkEdit,
    [TextEdit.block.kind]: TextEdit,
    [VideoEdit.block.kind]: VideoEdit,
};

export const useEditorsPreload = async () => {
    const { fulfilled } = useFetch(
        async () => Promise.all(Object.values(EDITORS_MAP).map(({ block }) => block.preload?.())),
        []
    );

    return fulfilled;
};

const curatePath = (url, path) => {
    const rootParts = url.split('/');
    const pathParts = path.split('/');

    let i = pathParts.length;
    const accumulator = [];

    while (i-- > 0) {
        const part = pathParts[i];

        accumulator.unshift([':kind', ':blockId'].includes(part) ? rootParts[i] : part);
    }

    return accumulator.join('/');
};

const matchRouteF = (location, to, fn) => {
    const matched = matchPath(location.pathname, to);

    return matched && fn(to.path, matched.params);
};

const matchRoute = (location, to, Component) => {
    const matched = matchPath(location.pathname, to);

    return matched && <Component key={to.path || to} {...matched.params} />;
};

const EditorsRouting = ({ location, match, store }) => {
    const workspace = useWorkspace();
    const website = useWebsiteData();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const [lastCollectionSlug, setLastCollectionSlug] = useState(false);
    const [firstLoad, setFirstLoad] = useState(true);
    const entries = Object.entries(EDITORS_MAP).map(([key, value]) => [toDashedCase(key), value]);
    const path = match.path;
    const root = generateUrl('.');

    const curated = curatePath(root, path);
    const paths = (kind, sub = '') =>
        Array.isArray(sub)
            ? [].concat(...sub.map((item) => paths(kind, item)))
            : [`${curated}/add-block/${kind}${sub}`, `${curated}/block/${kind}/:blockId${sub}`];

    const productPaths = useMemo(
        () => ({
            path: [
                ...paths(':kind', ['/p/:productSlug', '/c/:collectionSlug/p/:productSlug']),
                `${curated}/preview/:previewDevice(desktop|mobile)/p/:productSlug`,
                `${curated}/preview/:previewDevice(desktop|mobile)/c/:collectionSlug/p/:productSlug`,
            ],
            exact: false,
        }),
        [curated]
    );

    const collectionsPaths = useMemo(
        () => ({
            path: [
                ...paths(':kind', ['/c/:collectionSlug']),
                `${curated}/preview/:previewDevice(desktop|mobile)/c/:collectionSlug`,
                `${curated}/preview/:previewDevice(desktop|mobile)/c/:collectionSlug/p/:productSlug`,
            ],
            exact: false,
        }),
        [curated]
    );

    const cartPaths = useMemo(
        () => ({
            path: [
                `${curated}/preview/:previewDevice(desktop|mobile)/orders/:orderId`,
                `${curated}/preview/:previewDevice(desktop|mobile)/p/:productSlug/orders/:orderId`,
                `${curated}/preview/:previewDevice(desktop|mobile)/c/:collectionSlug/p/:productSlug/orders/:orderId`,
            ],
            exact: true,
        }),
        [curated]
    );

    const editors = (
        <AnimatePresence>
            {matchRoute(location, { path: `${curated}/add-block`, exact: true }, ChooseBlock)}

            {entries.map(([kind, EditorComponent]) => matchRoute(location, { path: paths(kind) }, EditorComponent))}

            <Route key="nested" exact={false} path={paths(':kind')} component={EditorsRouting} />

            {matchRouteF(location, { path: `${curated}/products` }, (path) => (
                <ProductsContextProvider key={path} base={root} websiteId={website._id}>
                    <Route key={path} component={ProductsPage} />
                </ProductsContextProvider>
            ))}

            {matchRoute(
                location,
                `${curated}/block/${kebabCase(ServicesGridBlockKind)}/:blockId/convert`,
                ConvertServices
            )}
        </AnimatePresence>
    );

    const products = (
        <AnimatePresence>
            {matchRouteF(location, collectionsPaths, (path, params) => {
                const collectionSlug = params.collectionSlug || lastCollectionSlug;
                const collection = collectionSlug && website.collections.find(({ slug }) => slug === collectionSlug);

                if (!collection) {
                    return null;
                }

                return (
                    <CollectionView
                        key={path}
                        workspaceId={workspace._id}
                        websiteId={website._id}
                        collection={collection}
                        firstLoad={firstLoad}
                        setFirstLoad={setFirstLoad}
                        setLastCollectionSlug={setLastCollectionSlug}
                    />
                );
            })}

            {matchRouteF(location, productPaths, (path, params) => (
                <ProductView
                    key={path}
                    workspaceId={workspace._id}
                    defaultCollectionId={website.defaultCollectionId}
                    lastCollectionSlug={lastCollectionSlug}
                    setLastCollectionSlug={setLastCollectionSlug}
                    interactive={!!store}
                    {...params}
                />
            ))}

            {matchRoute(location, { path: `${curated}/preview/:previewDevice(desktop|mobile)/user-orders/` }, UserOrdersPage)}
            {matchRoute(location, { path: `${curated}/preview/:previewDevice(desktop|mobile)/user-orders/:orderId`, exact: true }, UserOrderPage)}
            {matchRoute(location, cartPaths, OrderPage)}

            {matchRoute(location, `${curated}/preview/:previewDevice(desktop|mobile)/orders/:orderId/payment/:status`, PaymentStatusPage)}
        </AnimatePresence>
    );

    return (
        <>
            {editors}
            {store ? <Provider store={store}>{products}</Provider> : products}
        </>
    );
};

EditorsRouting.propTypes = {
    match: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    depth: PropTypes.number,
    store: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
};

export const EditBlockSheetRouting = ({ store }) => {
    const location = useLocation();
    const { path } = useRouteMatch();
    const [tipVisible, setTipVisible] = useLocalStorage('visualBuilderDragTip', true);
    const rootSharePath = '/:share(share|published|published/share|share/share)';
    const isReady = useEditorsPreload();

    if (!isReady) {
        return;
    }

    const match = (to, Component, props) => {
        const matched = matchPath(location.pathname, to);

        return matched && <Component key={to.path} {...matched.params} {...props} />;
    };

    return (
        <BlockEditContextProvider>
            <AnimatePresence>
                <Route path={path} render={(props) => <EditorsRouting store={store} {...props} />} />

                {match({ path: `${path}/guide`, exact: false }, GuideBuilder)}
                {match({ path: `${path}/published`, exact: false }, SharePublished)}
                {match({ path: `${path}/:share(share|published/share)`, exact: false }, ShareNew)}
                {match({ path: `${path}${rootSharePath}/name`, exact: false }, EditWebsiteName)}
                {match({ path: `${path}${rootSharePath}/name/domain`, exact: true }, WebsiteDomainEdit)}
                {match({ path: `${path}${rootSharePath}/embed`, exact: false }, Embed)}
                {match({ path: `${path}${rootSharePath}/qr`, exact: true }, Qr)}
                {match({ path: `${path}${rootSharePath}/socials`, exact: false }, SocialsShare)}
                {match({ path: `${path}${rootSharePath}/bio`, exact: false }, SocialsShareBio)}
                {match(
                    {
                        path: `${path}${rootSharePath}/:socials(socials|bio)/instagram`,
                        exact: true,
                    },
                    InstagramShareNew
                )}
                {match(
                    {
                        path: `${path}${rootSharePath}/:socials(socials|bio)/tiktok`,
                        exact: true,
                    },
                    TikTokShare
                )}

                {match({ path: `${path}/styling`, exact: true }, PageStyling)}
                {match({ path: `${path}/styling/theme`, exact: true }, PageTheme)}
                {match({ path: `${path}/styling/background-color`, exact: true }, PageStylingBackgroundColor)}
                {match({ path: `${path}/styling/background-image`, exact: true }, PageStylingBackgroundImage)}
                {match({ path: `${path}/styling/alignment`, exact: true }, PageStylingAlignment)}

                {/*{match({ path: `${path}/setup`, exact: true }, PageSetup)}*/}

                {match({ path: `${path}/help`, exact: true }, Help)}

                {/*{match({ path: `${path}/published`, exact: true }, PublishedMessage)}*/}
                {/*{match({ path: `${path}/not-published`, exact: true }, NotPublishedMessage)}*/}
                {match({ path: `${path}/not-published/no-blocks`, exact: true }, NoBlocksMessage)}

                {match({ path: `${path}/has-advanced-blocks`, exact: true }, HasAdvancedBlocksMessage)}
                {/*{match({ path: `${path}/settings/embedded`, exact: true }, () => (*/}
                {/*    <WebsiteEmbeddedEdit backPath="../.." />*/}
                {/*))}*/}

                {tipVisible &&
                    match({ path: `${path}/sort`, exact: true }, () => <SortHelp setTipVisible={setTipVisible} />)}
            </AnimatePresence>
        </BlockEditContextProvider>
    );
};

EditBlockSheetRouting.propTypes = {
    store: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
};
