import { useMemo } from 'react';
import { map, groupBy } from 'lodash';

import { checkAllRestrictionsOrThrow } from '@mssgme/shared';
import { useWorkspace } from '@mssgme/hooks';

function checkBlocks(rule, payload, blocks) {
    let warning;
    const kinds = [];

    blocks.forEach((block) => {
        try {
            checkAllRestrictionsOrThrow(rule, payload, block);
        } catch (error) {
            warning ||= error;
            kinds.push(block.kind);
        }
    });

    if (!warning) return null;

    const groupedKinds = map(groupBy(kinds), (item) => ({
        kind: item[0],
        qty: item.length,
    }));

    warning.blocks = groupedKinds;
    return warning;
}

function checkTheme(rule, payload, theme) {
    try {
        checkAllRestrictionsOrThrow(rule, payload, theme);
        return null;
    } catch (err) {
        return err;
    }
}

export function usePageCheck(page) {
    const workspace = useWorkspace();

    return useMemo(() => {
        if (!page) return null;

        let warning = checkBlocks('page-blocks-publish', { workspace }, page.blocks);
        warning ||= checkTheme('page-themes:edit', { workspace }, page.theme);

        return warning;
    }, [workspace, page]);
}
