import arrayMove from 'array-move';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useAsyncFn, useToggle } from 'react-use';
import { useHistory, useRouteMatch } from 'react-router-dom';

import { isMatchId, toDashedCase } from '@mssgme/helpers';
import { CustomCodeBlockKind } from '@mssgme/shared';
import { useActions, useFetching, useScrollbottomEffect } from '@mssgme/hooks';
import { useBlockCloning, useBlockDeletion, useBlockGrouping, useBlockToggle } from '../../../UI';
import { useBlockSelector } from './useBlockSelector';

const mapId = ({ _id }) => _id;

export const useBlocksMonitor = (website, page) => {
    const history = useHistory();
    const websiteId = website._id;
    const pageId = page._id;
    const { api } = useActions();
    const { url: baseUrl } = useRouteMatch();
    const [blocks, setBlocks] = useState(page.blocks);
    const [isMatrix, toggleMatrix] = useToggle(false);
    const [isSorting, setSorting] = useToggle(false);
    const [ref, scrollToBottom] = useScrollbottomEffect();
    const selection = useBlockSelector(blocks, page.blocks);
    const blockIds = useMemo(() => blocks.map(mapId), [blocks]);

    const handleBlockEdit = useCallback(
        (block) => history.push(`${baseUrl}/block/${toDashedCase(block.kind)}/${block._id}`),
        []
    );

    const handleBlockSelect = useCallback((block) => selection.handleSelectOne(block._id), []);
    const handleClearSelection = useCallback(() => selection.handleSelect([]), []);
    const handleBlockToggle = useCallback((block) => selection.handleToggle(block._id), []);
    const handleSortStart = useCallback(() => setSorting(true), []);
    const handleSortEnd = useFetching(
        async ({ oldIndex, newIndex }) => {
            setSorting(false);

            if (oldIndex === newIndex) {
                return;
            }

            setBlocks(arrayMove(blocks, oldIndex, newIndex));

            try {
                await api.blocks.sort({
                    pageId,
                    websiteId,
                    payload: { oldIndex, newIndex },
                });
            } catch (e) {
                setBlocks(blocks);

                throw e;
            }
        },
        [websiteId, pageId, blocks]
    );

    const handleMoveBlock = useCallback(
        async (block, delta) => {
            const oldIndex = blocks.findIndex(isMatchId(block._id));
            const newIndex = oldIndex + delta;

            if (newIndex < 0 || newIndex >= blocks.length) {
                return;
            }

            await handleSortEnd.run({ oldIndex, newIndex });
        },
        [blocks, handleSortEnd.run]
    );

    const { isShown: isDeleting, initiate: initiateDeletion, handle: handleDeletion } = useBlockDeletion(
        handleClearSelection
    );
    const { run: handleToggling } = useBlockToggle();
    const { run: handleGrouping } = useBlockGrouping();
    const { run: handleCloning } = useBlockCloning(
        useCallback(
            (ctx, lastState) => {
                const added = lastState.filter(({ _id }) => !blocks.find(isMatchId(_id))).map(mapId);

                selection.handleSelect(added);
            },
            [blocks]
        )
    );
    const [{ loading: isPerformingAction }, handleAction] = useAsyncFn(
        async ({ selection, action, enable, group }) => {
            if (!selection.length) {
                return;
            }

            const ids = selection.map(mapId);

            switch (action) {
                case 'up':
                case 'down': {
                    const isUp = action === 'up';
                    const delta = isUp ? -1 : 1;
                    const ordered = isUp ? selection : selection.slice().reverse();

                    for (const block of ordered) {
                        await handleMoveBlock(block, delta);
                    }

                    const el = document.getElementById(`block-${selection[0]._id}`);

                    if (el) {
                        el.scrollIntoView?.({
                            behavior: 'smooth',
                            block: 'nearest',
                        });
                    }

                    return;
                }

                case 'remove':
                    return initiateDeletion({ websiteId, pageId, payload: { blockId: ids } });

                case 'clone':
                    return handleCloning({ websiteId, pageId, payload: { blockId: ids } });

                case 'group':
                    return handleGrouping({ websiteId, pageId, payload: { blockId: ids, group } });

                case 'toggle':
                    return handleToggling({ websiteId, pageId, payload: { blockId: ids, enable } });
            }
        },
        [handleMoveBlock, websiteId, pageId]
    );
    const isDisabledDashboard = isPerformingAction || isDeleting;

    useEffect(() => {
        const newLastIdx = page.blocks.length - 1;
        const newLastBlock = page.blocks[newLastIdx];
        const isNewBlockAdded = blocks.length < page.blocks.length;
        const isLastBlockDifferent = blocks[blocks.length - 1]?._id !== newLastBlock?._id;

        setBlocks(page.blocks);

        if (!(isNewBlockAdded && isLastBlockDifferent)) {
            return;
        }

        if (newLastBlock.kind === CustomCodeBlockKind) {
            toggleMatrix(true);
        }

        return scrollToBottom();
    }, [page.blocks]);

    return {
        ref,
        isDeleting,
        isDisabledDashboard,
        isMatrix,
        isSorting,
        blockIds,
        blocksHash: blockIds.join(','),
        blocks,
        setBlocks,
        selection,
        toggleMatrix,
        handleBlockEdit,
        handleBlockSelect,
        handleClearSelection,
        handleBlockToggle,
        handleSortEnd,
        handleSortStart,
        handleAction,
        handleDeletion,
    };
};
