import { useState } from 'react';

import { useActions, useFetch } from '@mssgme/hooks';

const STATE_NEW = 'new';
const STATE_WAIT_SSL = 'wait_ssl';
const STATE_READY = 'ready';

export const useDomain = (website) => {
    const { api } = useActions();
    const [resolved, setResolved] = useState({ domain: '', state: '', dns_records: null });

    useFetch(async () => {
        if (website.domain.name) {
            const fetchedStatus = await api.websites.domain.get({ websiteId: website._id });

            return setResolved(fetchedStatus);
        }

        setResolved({ domain: '', state: '' });
    }, [website.domain.name]);

    return {
        ...resolved,
        isNew: resolved.state === STATE_NEW,
        isWaitingSSl: resolved.state === STATE_WAIT_SSL,
        isReady: resolved.state === STATE_READY,
        isEmpty: !resolved.state,
        DNSRecords: resolved.dns_records,
    };
};
