import React, { useCallback, useMemo } from 'react';
import { matchPath, useHistory, useLocation, useRouteMatch } from 'react-router-dom';
import { useToggle } from 'react-use';

import { isUploadUsedOnWebsite } from '@mssgme/shared';
import { clearWebsitePayload, useActions, useFetching, useNavigationUtils, useRestriction } from '@mssgme/hooks';
import { usePageCheck } from '../../hooks';

// eslint-disable-next-line jsdoc/require-returns
/**
 * @param {*} website Website
 * @param {*} page Page
 * @param {{published: string}} [routes] Custom routes to navigate to
 */
export const usePublish = (website, page, routes) => {
    const history = useHistory();
    const location = useLocation();
    const { path: routePath } = useRouteMatch();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { api } = useActions();
    const warning = usePageCheck(page);
    const { warning: galleryWarning } = useRestriction('page-extended-gallery', website);
    const [isFinishing, toggleFinishing] = useToggle(false);
    const path = `/pages/-/${website._id}`;
    const successPath = { pathname: generateUrl(routes?.published || '.'), state: { from: 'publishedMessage' } };
    const unusedAssets = useMemo(
        () => galleryWarning && website.uploads.filter((upload) => !isUploadUsedOnWebsite(upload, website)).length,
        [galleryWarning, website]
    );

    const match = (to, Component) => {
        const matched = matchPath(location.pathname, to);

        return matched && <Component key={to.path} {...matched.params} />;
    };

    const handleClose = useCallback(() => {
        toggleFinishing(false);
    }, []);

    const handleSuccess = useCallback(() => {
        toggleFinishing(false);
        history.push(successPath);
    }, [successPath]);

    const handlePublish = useFetching(async () => {
        if (page.blocks.length) {
            if (warning) {
                return history.push(generateUrl('has-advanced-blocks'));
            }

            await api.websites.update({
                websiteId: website._id,
                payload: { ...clearWebsitePayload(website), enabled: true, scaffold: false },
            });

            handleSuccess();
        } else {
            history.push(`${path}/not-published/no-blocks`);
        }
    }, [website, page, path, handleSuccess, warning]);

    return {
        isFinishing,
        ...handlePublish,
        successPath,
        handleClose,
        handleSuccess,
        canPublish: !warning,
        unusedAssets,
        routePath,
        match,
    };
};
