import React, { useMemo } from 'react';
import { matchPath, useLocation, useRouteMatch } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';

import { useWebsiteBySlug } from '@mssgme/hooks';

import { WebsiteEditorContext } from '../../../hooks';
import {
    Qr,
    SocialsShare,
    InstagramShareNew,
    TikTokShare,
    NotPublishedMessage,
    NoBlocksMessage,
    Embed,
    HasAdvancedBlocksMessage,
    ShareNew,
    SharePublished,
    SocialsShareBio,
    EditWebsiteName,
    // PageSetup,
} from '../../Page';
import { TemplateCreatePage } from '../../Template';
import {
    WebsiteTrackingPlatformEdit,
    WebsiteSeoEdit,
    WebsiteUrlEdit,
    WebsiteNameEdit,
    WebsiteStatusEdit,
    WebsiteBrandingEdit,
    WebsiteDelete,
    WebsiteDomainEdit,
    WebsiteEmbeddedEdit,
    WebsiteLocaleEdit,
} from '../Edit';
import { FeedbackDetails, FeedbackList } from '../../Feedback';
import { WebsiteSettings } from './Settings';
import { WebsiteTracking } from './Tracking';
import { WebsiteAnalytics } from './Analytics';
import { WebsiteAudience } from './Audience';

export const WebsiteRouter = () => {
    const location = useLocation();
    const { path, params } = useRouteMatch();
    const { _id: websiteId } = useWebsiteBySlug(params.websiteSlug);
    const context = useMemo(() => ({ websiteId, pageId: null, blockId: null }), [websiteId]);
    const rootSharePath = '/:share(share|published|published/share|share/share)';

    const match = (to, Component, props) => {
        const matched = matchPath(location.pathname, to);

        return matched && <Component key={to.path} {...matched.params} {...props} />;
    };

    return (
        <WebsiteEditorContext.Provider value={context}>
            <AnimatePresence>
                {match({ path: `${path}/published`, exact: false }, SharePublished)}
                {match({ path: `${path}/:share(share|published/share)`, exact: false }, ShareNew)}
                {match({ path: `${path}/not-published`, exact: true }, NotPublishedMessage)}
                {match({ path: `${path}/not-published/no-blocks`, exact: true }, NoBlocksMessage)}

                {match({ path: `${path}/has-advanced-blocks`, exact: true }, HasAdvancedBlocksMessage)}

                {match({ path: `${path}${rootSharePath}/name`, exact: false }, EditWebsiteName)}
                {match({ path: `${path}${rootSharePath}/name/domain`, exact: true }, WebsiteDomainEdit)}
                {match({ path: `${path}${rootSharePath}/embed`, exact: true }, Embed)}
                {match({ path: `${path}${rootSharePath}/qr`, exact: true }, Qr)}
                {match({ path: `${path}/standalone/qr`, exact: true }, Qr)}
                {match({ path: `${path}${rootSharePath}/socials`, exact: false }, SocialsShare)}
                {match({ path: `${path}${rootSharePath}/bio`, exact: false }, SocialsShareBio)}
                {match(
                    {
                        path: `${path}${rootSharePath}/:socials(socials|bio)/instagram`,
                        exact: true,
                    },
                    InstagramShareNew
                )}
                {match(
                    {
                        path: `${path}${rootSharePath}/:socials(socials|bio)/tiktok`,
                        exact: true,
                    },
                    TikTokShare
                )}

                {/*{match({ path: `${path}/setup`, exact: false }, PageSetup)}*/}

                {match({ path: `${path}/settings`, exact: false }, WebsiteSettings)}
                {match({ path: `${path}/settings/tracking`, exact: false }, WebsiteTracking)}
                {match({ path: `${path}/settings/tracking/:platform`, exact: true }, WebsiteTrackingPlatformEdit)}
                {match({ path: `${path}/settings/seo`, exact: true }, WebsiteSeoEdit)}
                {match({ path: `${path}/settings/url`, exact: true }, WebsiteUrlEdit)}
                {match({ path: `${path}/settings/domain`, exact: true }, WebsiteDomainEdit)}
                {match({ path: `${path}/settings/name`, exact: true }, WebsiteNameEdit)}
                {match({ path: `${path}/settings/locale`, exact: true }, WebsiteLocaleEdit)}
                {match({ path: `${path}/settings/embedded`, exact: true }, WebsiteEmbeddedEdit)}
                {match({ path: `${path}/settings/status`, exact: true }, WebsiteStatusEdit)}
                {match({ path: `${path}/settings/branding`, exact: true }, WebsiteBrandingEdit)}
                {match({ path: `${path}/settings/no-blocks`, exact: true }, NoBlocksMessage)}
                {match({ path: `${path}/settings/template`, exact: true }, TemplateCreatePage)}

                {match({ path: `${path}/settings/delete`, exact: true }, WebsiteDelete)}
                {match({ path: `${path}/analytics`, exact: true }, WebsiteAnalytics)}
                {match({ path: `${path}/audience`, exact: true }, WebsiteAudience)}

                {match({ path: `${path}/feedback`, exact: false }, FeedbackList)}
                {match({ path: `${path}/feedback/:feedbackId`, exact: true }, FeedbackDetails)}
            </AnimatePresence>
        </WebsiteEditorContext.Provider>
    );
};
