import PropTypes from 'prop-types';
import React from 'react';
import cn from 'classnames';
import { createPortal } from 'react-dom';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { AnimatePresence } from 'framer-motion';

import { useNavigationUtils } from '@mssgme/hooks';
import { Button, LandingsPropTypes, TextField } from '@mssgme/ui';

import { WebsiteDomainEdit } from '../../Screens/Website';
import { WebsiteEditForm } from '../Form';
import { usePublish } from './usePublish';

import styles from './PublishButton.scss';

export const PublishButton = ({ routes, website, page, ...rest }) => {
    const { t } = useTranslation('landings');
    const {
        pending,
        run: handlePublish,
        isFinishing,
        handleClose,
        handleSuccess,
        routePath,
        match,
        unusedAssets,
    } = usePublish(website, page, routes);
    const { generateUrl } = useNavigationUtils();
    const domainName = website.domain?.name;

    return (
        <>
            {!website.enabled && <Button onClick={handlePublish} isFetching={pending} {...rest} />}
            {createPortal(
                <AnimatePresence>
                    {isFinishing && (
                        <WebsiteEditForm
                            website={website}
                            initialData={{ scaffold: false, enabled: true }}
                            title={t('Website.Edit.PublishButton.heading')}
                            backPath={handleClose}
                            successRedirectPath={handleSuccess}
                            saveButtonTitle={t('Website.Edit.PublishButton.applyButton')}
                            noGuardChanges
                            initialEnabledSubmitButton
                            hint={
                                !!unusedAssets && (
                                    <div className={styles.unusedPhotosHint}>
                                        {t('Website.Edit.PublishButton.unused', { count: unusedAssets })}
                                    </div>
                                )
                            }
                        >
                            {() => (
                                <>
                                    <div style={{ margin: '0 0 20px 0', textAlign: 'center' }}>
                                        <p style={{ margin: 0 }}>{t('Website.Edit.PublishButton.fieldTip')}</p>
                                    </div>
                                    {/*<TextField*/}
                                    {/*    builderInput*/}
                                    {/*    name="name"*/}
                                    {/*    id="website_name"*/}
                                    {/*    label={t('Website.fields.name.label')}*/}
                                    {/*    placeholder={t('Website.fields.name.placeholder')}*/}
                                    {/*    maxLength={30}*/}
                                    {/*/>*/}
                                    <TextField
                                        builderInput
                                        name="slug"
                                        beforeInput={t('common:label.host')}
                                        label={t('Website.fields.slug.publishLabel')}
                                        placeholder={t('Website.fields.slug.placeholder')}
                                        maxLength={50}
                                        autoCapitalize="off"
                                        controlProps={{ style: { textTransform: 'lowercase' } }}
                                    />
                                    <div className={styles.domainPreview}>
                                        <span className={styles.label}>
                                            {t('Website.Edit.PublishButton.domain.label')}
                                        </span>
                                        <div className={styles.preview}>
                                            <div className={styles.value}>
                                                <span className={cn({ [styles.empty]: !domainName })}>
                                                    {domainName || 'www.example.com'}
                                                </span>
                                            </div>
                                            <div className={styles.after}>
                                                <Link to={generateUrl('domain')}>
                                                    {domainName
                                                        ? t('Website.Edit.PublishButton.domain.buttons.change')
                                                        : t('Website.Edit.PublishButton.domain.buttons.connect')}
                                                </Link>
                                            </div>
                                        </div>
                                    </div>
                                </>
                            )}
                        </WebsiteEditForm>
                    )}
                    {isFinishing && match({ path: `${routePath}/domain`, exact: true }, WebsiteDomainEdit)}
                </AnimatePresence>,
                document.getElementById('mainRoot')
            )}
        </>
    );
};

PublishButton.propTypes = {
    routes: PropTypes.object,
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
};
