import PropTypes from 'prop-types';
import React, { useMemo, useRef } from 'react';

import { useForceUpdate } from '@mssgme/hooks';
import { CoreContext, makeCoreRouter } from '../hooks';

export function CoreContextProvider({ base, children }) {
    const update = useForceUpdate();
    const router = useRef();

    const context = useMemo(() => {
        router.current = null;

        const ctx = {
            path: `${base}`,
            get router() {
                return router.current || (router.current = makeCoreRouter(ctx));
            },
            setEntry(path) {
                const current = router.current?.toEntry();

                if (path) {
                    if (path === current) {
                        return;
                    }

                    router.current = makeCoreRouter({ ...ctx, back: path, isBackOverriddenBack: true });
                } else if (current && base !== current) {
                    router.current = makeCoreRouter(ctx);
                } else {
                    return;
                }

                update();
            },
        };

        return ctx;
    }, [base]);

    return <CoreContext.Provider value={context}>{children}</CoreContext.Provider>;
}

CoreContextProvider.propTypes = {
    base: PropTypes.string.isRequired,
};
