import React, { useCallback } from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useWebsitesFactory, useFeature } from '@mssgme/hooks';
import { Button, ProtectAccess } from '@mssgme/ui';

export function CreateWebsiteButton() {
    const { t } = useTranslation('landings');
    const { link, pending, create } = useWebsitesFactory();
    const templatesEnabled = true; // useFeature('templates');

    const createWebsite = useCallback(() => create(), [create]);

    if (templatesEnabled) {
        return (
            <ProtectAccess strict rule="websites:create">
                {(onClick) => (
                    <Button
                        fullWidth
                        rounded
                        style={{
                            height: 56,
                            fontSize: 16,
                            lineHeight: '24px',
                            boxShadow: '1px 2px 6px rgba(0, 0, 0, 0.08)',
                        }}
                        tag={Link}
                        to={link}
                        onClick={onClick}
                    >
                        + {t('labels.website.createNew')}
                    </Button>
                )}
            </ProtectAccess>
        );
    }

    return (
        <ProtectAccess rule="websites:create">
            {(onClick) => (
                <Button
                    fullWidth
                    rounded
                    style={{
                        height: 56,
                        fontSize: 16,
                        lineHeight: '24px',
                        boxShadow: '1px 2px 6px rgba(0, 0, 0, 0.08)',
                    }}
                    onClick={onClick || createWebsite}
                    isFetching={pending}
                >
                    + {t('labels.website.createNew')}
                </Button>
            )}
        </ProtectAccess>
    );
}

CreateWebsiteButton.propTypes = {};
