import React from 'react';
import { useTranslation } from 'react-i18next';
import { Redirect, useParams } from 'react-router-dom';
import { FaUserTag } from 'react-icons/fa';

import {
    useBilling,
    useNavigationUtils,
    useUpgradeLocation,
    useWebsiteById,
    useWebsiteBySlug,
    useWebsitePage,
} from '@mssgme/hooks';
import {
    LinkButton,
    Nav,
    NavItemLink,
    PersonalLink,
    PersonalLinkNav,
    RestrictionScope,
    SecondLevelScreen,
} from '@mssgme/ui';
import { Edit2Icon, PieChartIcon, SettingsIcon, UsersIcon } from '@mssgme/icons';
import { ProductsIcon } from '@mssgme/products';
import { useDomain, useSingleWebsiteFeedbackCounts } from '../../../../hooks';
import { BanBanner, FeedbacksBadge, PublishButton } from '../../../../UI';
import { WebsiteRouter } from '../WebsiteRouter';

export function WebsiteHome() {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { name, absoluteUrl } = useWebsiteById(website._id);
    const { isFree } = useBilling();
    const { isWaitingSSl, isReady } = useDomain(website);
    const feedbacks = useSingleWebsiteFeedbackCounts(website._id);
    const upgradeUrl = useUpgradeLocation();
    const isPublished = page.enabled;
    const websiteUrl = isWaitingSSl || isReady ? `http${isReady ? 's' : ''}://${website.domain.name}` : absoluteUrl;

    if (!page) {
        return <Redirect to={generateUrl('..')} />;
    }

    const topContent = (
        <>
            <PersonalLink
                style={{ margin: '12px 0 8px 0' }}
                url={websiteUrl}
                // textToCopy={absoluteUrl}
                website={website}
            />
            {isPublished && (
                <PersonalLinkNav
                    // embedTo={generateUrl('share/embed')}
                    sharePageTo={generateUrl('share')}
                    qrPageTo={{ pathname: generateUrl('standalone/qr'), state: { backPath: '../..' } }}
                    textToCopy={websiteUrl}
                    website={website}
                    style={{ marginTop: 16 }}
                />
            )}
        </>
    );

    return (
        <>
            <SecondLevelScreen
                title={name}
                backPath={generateUrl('../..')}
                titleRightContent={
                    <>
                        <PublishButton
                            btnStyle="primary"
                            style={{
                                padding: '0 16px',
                                fontWeight: 500,
                                height: 36,
                                borderRadius: 8,
                                marginLeft: 5,
                            }}
                            website={website}
                            page={page}
                            routes={{ published: 'published' }}
                            data-button="publish"
                        >
                            {t('Editor.Dashboard.header.publish')}
                        </PublishButton>
                        {isPublished && isFree && (
                            <LinkButton
                                outline
                                btnStyle="primary"
                                to={upgradeUrl}
                                style={{
                                    border: '1px solid',
                                    backgroundColor: 'rgba(41, 121, 255, 0.05)',
                                    padding: '0 16px',
                                    fontWeight: 500,
                                    height: 36,
                                    borderRadius: 8,
                                    marginLeft: 5,
                                }}
                                data-button="website-home-upgrade-button"
                                data-event="click-upgrade"
                                data-variant="button"
                            >
                                {t('common:misc.upgrade')}
                            </LinkButton>
                        )}
                    </>
                }
                topPanelChildren={topContent}
            >
                <RestrictionScope>
                    <Nav>
                        <NavItemLink to={generateUrl('edit')} icon={Edit2Icon}>
                            {t('Website.View.Home.items.editor')}
                        </NavItemLink>
                        <NavItemLink to={generateUrl('audience')} icon={UsersIcon}>
                            {t('Website.View.Home.items.audience')}
                        </NavItemLink>
                        <NavItemLink to={generateUrl('analytics')} icon={PieChartIcon}>
                            {t('Website.View.Home.items.analytics')}
                        </NavItemLink>
                        <NavItemLink
                            to={generateUrl('feedback')}
                            icon={FaUserTag}
                            chevron={<FeedbacksBadge {...feedbacks} />}
                        >
                            {t('Website.View.Home.items.feedback')}
                        </NavItemLink>
                        <NavItemLink to={generateUrl('products')} icon={ProductsIcon}>
                            {t('Website.View.Home.items.products')}
                        </NavItemLink>
                        <NavItemLink to={generateUrl('settings')} icon={SettingsIcon}>
                            {t('Website.View.Home.items.settings')}
                        </NavItemLink>
                    </Nav>
                </RestrictionScope>

                {website.moderation?.blocked && <BanBanner moderation={website.moderation} />}
            </SecondLevelScreen>
            <WebsiteRouter />
        </>
    );
}
