import React, { useMemo, useContext, useCallback } from 'react';

import { useFetch, useNavigationUtils } from '@mssgme/hooks';

import { BLOCK_FACTORIES } from './factories';

const BlockEditContext = React.createContext();

const initialState = { callback: null, mixin: null };
const reducer = (state, action) => {
    switch (action.type) {
        case 'callback':
            return { ...state, callback: action.callback };

        case 'mixin':
            return { ...state, mixin: action.mixin };

        case 'reset':
            return initialState;
    }
};
export const BlockEditContextProvider = ({ children }) => {
    const [{ callback, mixin }, dispatch] = React.useReducer(reducer, initialState);
    const setCallback = useCallback((callback) => dispatch({ type: 'callback', callback }), [dispatch]);
    const setMixin = useCallback((mixin) => dispatch({ type: 'mixin', mixin }), [dispatch]);
    const reset = useCallback(() => dispatch({ type: 'reset' }), [dispatch]);

    return (
        <BlockEditContext.Provider value={{ callback, mixin, setCallback, setMixin, reset }}>
            {children}
        </BlockEditContext.Provider>
    );
};

/**
 * @returns {{ callback: Function, mixin: object|null, setCallback: Function, setMixin: Function, reset: Function }} Context
 */
export const useBlockEditContext = () => {
    const context = useContext(BlockEditContext);

    if (!context) {
        throw new Error('BlockEditContext provider is missing in the hierarchy');
    }

    return context;
};

export const useBlockEditor = ({ blockId, as, website, page, t, ...rest }) => {
    const { generateUrl } = useNavigationUtils();
    const { callback, mixin } = useBlockEditContext();
    const isNew = !blockId;
    const paths = useMemo(
        () => ({
            basePath: generateUrl('.'),
            backPath: generateUrl('.', { useMatchUrl: true }),
            successRedirectPath: generateUrl('.', { useMatchUrl: true }),
        }),
        []
    );
    const blockType = as.block.kind;
    const factory = BLOCK_FACTORIES[blockType];
    const block = isNew ? undefined : page.blocks.find((block) => block._id === blockId);
    const initialValues = useMemo(
        () => ({
            ...(block && !isNew ? block : factory({ kind: blockType, enabled: true }, t)),
            ...mixin,
        }),
        [isNew, blockType, block, mixin]
    );
    const isDeleted = !!(blockId && !initialValues._id);
    const preload = as.block.preload;
    const preloader = () => preload?.();
    const { pending } = useFetch(preloader, [as.block]);

    if (pending && preload) {
        throw preloader();
    }

    return {
        isNew,
        bind: {
            websiteId: website._id,
            pageId: page._id,
            blockId,
            page,
            block,
            blockType,
            initialValues,
            isDeleted,
            isSafe: !callback,
            blockComponent: as.block,
            stylingPath: `blocks.${blockType.toLowerCase()}`,
            themePropPath: 'theme',
            canToggle: false,
            ...paths,
            ...rest,
        },
    };
};
