import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { Link } from '@mssgme/userpage-core';
import { SmartLinkField } from '@mssgme/ui';
import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockEditor } from '../../../components';

const themeFields = {
    social: 'theme.blocks.link.icon',
    color: 'theme.blocks.link.backgroundColor',
    contrastedColor: 'theme.blocks.link.contrastedColor',
};

export function LinkEdit({ blockId }) {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { bind } = useBlockEditor({ website, page, blockId, as: LinkEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Link.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <SmartLinkField required name="link" website={website} themeFields={themeFields} />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

LinkEdit.propTypes = {
    blockId: PropTypes.string,
};

LinkEdit.block = Link;
