import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { prettifyUrl } from '@mssgme/shared';
import { LandingsPropTypes } from '../../../domainTypes';
import StyledSelectField from '../StyledSelectField';

export default function SelectPageField({
    website: { homePageId, name: websiteName, pages, absoluteUrl },
    name,
    ...rest
}) {
    const { t } = useTranslation('ui');
    const options = pages.map((page) => ({
        page,
        label: page._id === homePageId ? websiteName : page.name || 'labels.unnamedPage',
        url: page._id === homePageId ? absoluteUrl : page.absoluteUrl,
        value: page._id,
    }));

    return (
        <StyledSelectField
            name={name}
            placeholder={t('SelectPageField.placeholder')}
            isSearchable
            isClearable
            options={options}
            formatOptionLabel={(option, { context }) => (
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <div>{option.label}</div>
                    {context === 'menu' && (
                        <div style={{ fontSize: '80%', marginTop: 2, opacity: 0.6 }}>{prettifyUrl(option.url)}</div>
                    )}
                </div>
            )}
            {...rest}
        />
    );
}

SelectPageField.propTypes = {
    name: PropTypes.string.isRequired,
    website: LandingsPropTypes.website.isRequired,
};
