import PropTypes from 'prop-types';
import React, { useCallback, useRef } from 'react';
import { useTranslation } from 'react-i18next';

import { TextareaField } from '@mssgme/ui';
import { ArrayField, ListItemSchema } from '../../../components';

const ListItem = ({ value, className, textClassName }) => (
    <div className={className}>
        <div className={textClassName}>{value.content.replace(/\n/g, ' ')}</div>
    </div>
);

ListItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const ListItemsField = (props) => {
    const { t } = useTranslation('landings');
    const { current: labels } = useRef({
        addButton: {
            more: t('Page.Edit.List.itemAddMore'),
            first: t('Page.Edit.List.itemAdd'),
        },
        modal: {
            title: t('Page.Edit.List.itemFormHeading'),
        },
    });
    const itemFactory = useCallback(() => ({ content: '' }));

    return (
        <ArrayField factory={itemFactory} labels={labels} Item={ListItem} validationSchema={ListItemSchema} {...props}>
            <TextareaField
                required
                builderInput
                name="content"
                // label={t('Page.Edit.List.fields.content.label')}
                placeholder={t('Page.Edit.List.fields.content.placeholder')}
                maxLength={800}
            />
        </ArrayField>
    );
};

ListItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
