import React from 'react';
import { matchPath, Redirect, Route, Switch, useLocation, useParams } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';

import { ThemeContext } from '@mssgme/ui';
import { LanguageContext, useNavigationUtils, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { ProductsPage } from '@mssgme/products';
import { useAutoTheme, usePageFrontendTranslations } from '@mssgme/userpage-core';

import { LandingContextsProvider } from '../UI';
import { WebsiteHome, WebsitesList, WebsiteChooseTemplate, WebsiteCreate } from './Website';
import { VisualBuilder } from './Page';
import { TemplateHome, TemplatesList } from './Template';

const WebsiteRouter = () => {
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });

    if (!page) {
        return <Redirect to={generateUrl('/pages')} />;
    }

    return (
        <LandingContextsProvider base={`/pages/-/${websiteSlug}`} website={website} page={page}>
            <Switch>
                <Route path="/pages/-/:websiteSlug/edit" component={VisualBuilder} />
                <Route path="/pages/-/:websiteSlug/products" component={ProductsPage} />
                <Route path="/pages/-/:websiteSlug" component={WebsiteHome} />
            </Switch>
        </LandingContextsProvider>
    );
};

export function LandingsPage() {
    const location = useLocation();
    const { theme } = useAutoTheme();
    const { i18n, isLoaded } = usePageFrontendTranslations();

    if (!isLoaded) {
        return null;
    }

    const match = (to, Component) => {
        const matched = matchPath(location.pathname, to);

        return matched && <Component key={to.path} {...matched.params} />;
    };

    return (
        <LanguageContext.Provider value={i18n.language}>
            <ThemeContext.Provider value={theme}>
                <Switch>
                    <Route exact path={['/pages', '/pages/create']} component={WebsitesList} />
                    <Route path="/s" component={WebsitesList} />
                    <Route path="/pages/-/:websiteSlug" component={WebsiteRouter} />
                    <Route exact path="/pages/templates" component={TemplatesList} />
                    <Route path="/pages/templates/:templateId" component={TemplateHome} />
                </Switch>

                <AnimatePresence>
                    {match({ path: '/pages/create', exact: true }, WebsiteChooseTemplate)}
                </AnimatePresence>
            </ThemeContext.Provider>
        </LanguageContext.Provider>
    );
}
