import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useToggle } from 'react-use';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { merge, pick } from 'lodash';

import { useActions } from '@mssgme/hooks';
import { LandingsPropTypes, Form, EnhancedFormik, SaveButton, usePresenceRemoval, ModalEditPane } from '@mssgme/ui';
import { useThemeStylingDefinitions } from '../../Theming';
import { WebsiteEditorContext } from '../../../hooks';

import styles from './PageEditForm.scss';

const PAGE_FIELDS = ['enabled', 'name', 'slug', 'seo', 'tracking', 'theme'];

export function PageEditForm({ websiteId, page, children, title, backPath, blocked }) {
    const pageId = page && page._id;
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const [isClosing, setClosing] = useToggle(false);
    const context = useMemo(() => ({ websiteId, pageId, blockId: null }), [websiteId, pageId]);
    const getThemingForPath = useThemeStylingDefinitions();
    const styling = getThemingForPath('theme', 'page');
    const isNew = !pageId;
    const initialValues = merge(
        {
            enabled: true,
            name: '',
            slug: '',
            seo: { title: '', description: '' },
            tracking: { facebook: '', google: '', yandex: '' },
            theme: {},
        },
        pick(page, PAGE_FIELDS)
    );

    const handleSubmit = useCallback(
        async (values) => {
            const payload = values; // { ...values, theme: styling.enrich(values.theme) };

            if (isNew) {
                await api.pages.create({ websiteId, payload });
            } else {
                await api.pages.update({ websiteId, pageId, payload });
            }

            setClosing(true);
            setTimeout(() => history.push(backPath), 200);
        },
        [websiteId, pageId, styling]
    );

    usePresenceRemoval();

    return (
        <EnhancedFormik initialValues={initialValues} onSubmit={handleSubmit}>
            {(form) => (
                <WebsiteEditorContext.Provider value={context}>
                    <ModalEditPane
                        guardChanges={!isClosing && !blocked}
                        onClose={backPath}
                        title={title}
                        rightButton={
                            <SaveButton
                                noborder
                                nopadding
                                className={styles.saveButton}
                                btnStyle="transparent"
                                size="small"
                                isInitialHidden={false}
                                disabled={isClosing || !form.dirty}
                                isFetching={form.isSubmitting}
                                onClick={blocked}
                            >
                                {isNew ? t('labels.page.create') : t('ui:labels.apply')}
                            </SaveButton>
                        }
                    >
                        <Form autoComplete="off">{children({ ...form, styling })}</Form>
                    </ModalEditPane>
                </WebsiteEditorContext.Provider>
            )}
        </EnhancedFormik>
    );
}

PageEditForm.propTypes = {
    websiteId: PropTypes.string.isRequired,
    page: LandingsPropTypes.page,
    children: PropTypes.func.isRequired,
    title: PropTypes.string.isRequired,
    backPath: PropTypes.string,
    blocked: PropTypes.func,
};

PageEditForm.defaultProps = {
    page: null,
    backPath: '/pages',
};
