import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { useHistory, useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { Button, CopyLink, ModalPane } from '@mssgme/ui';
import InstaTipImage from '../../../../img/instagramTip.jpg';

import { useDomain } from '../../../../hooks';

import styles from './InstagramShareNew.scss';

export function InstagramShareNew({ backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const history = useHistory();
    const historyBackPath = history?.location?.state?.backPath;
    const website = useWebsiteBySlug(websiteSlug);
    const {
        page: { absoluteUrl },
    } = useWebsitePage(website);
    const { isWaitingSSl, isReady } = useDomain(website);
    const websiteUrl = isWaitingSSl || isReady ? `http${isReady ? 's' : ''}://${website.domain.name}` : absoluteUrl;
    const closePath = generateUrl(historyBackPath || backPath);

    return (
        <ModalPane title={t('Editor.InstagramShareNew.title')} onClose={closePath}>
            <div className={styles.content}>
                <ol>
                    <li dangerouslySetInnerHTML={{ __html: t('Editor.InstagramShareNew.steps.1') }} />
                </ol>
                <CopyLink className={styles.userLink} href={websiteUrl} />
                <ol start="2">
                    <li dangerouslySetInnerHTML={{ __html: t('Editor.InstagramShareNew.steps.2') }} />
                    <li dangerouslySetInnerHTML={{ __html: t('Editor.InstagramShareNew.steps.3') }} />
                </ol>
                <img className={styles.tipImg} src={InstaTipImage} alt="Tip" />
                <Button
                    fullWidth
                    tag="div"
                    btnStyle="transparent"
                    style={{ fontSize: 16, marginBottom: 16 }}
                    onClick={() => window.open('https://www.instagram.com/', '_blank')}
                >
                    {t('Editor.InstagramShareNew.openInstagram')}
                </Button>
            </div>
        </ModalPane>
    );
}

InstagramShareNew.propTypes = {
    backPath: PropTypes.string,
};

InstagramShareNew.defaultProps = {
    backPath: '..',
};
