import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';

import { cls } from '@mssgme/helpers';
import { useRestriction } from '@mssgme/hooks';
import { LandingsPropTypes } from '../../domainTypes';
import { PlanBadge } from '../PlanBadge';
import { BlockListItemPreviewList } from './BlockListItemPreviewList';
import { BlockListItemPreviewWithACL } from './BlockListItemPreviewWithACL';

import styles from './BlockListItemPreview.scss';

export function BlockListItemPreview({
    tag: Tag,
    label,
    subLabel,
    labelIcon: Icon,
    labelColor,
    iconColor,
    rightIcon,
    previewType,
    preview,
    disabled,
    onClick,
    noOnClick,
    asterisksRestrictMode,
    restrict = {},
    ...rest
}) {
    const { rule, subject, exclusive, iconOnly } = restrict;
    const { warning, handleBlockedEvent } = useRestriction(rule, subject);

    if (warning && exclusive) {
        return null;
    }

    const rightNode = (warning && !asterisksRestrictMode) ? (
        <PlanBadge plan={warning.target} />
    ) : (
        rightIcon
    );
    const interactiveProps = (!noOnClick) && { onClick: (!iconOnly && handleBlockedEvent) || onClick };

    return (
        <Tag
            {...interactiveProps}
            {...cls([styles.root, disabled && styles.disabled, !!interactiveProps && styles.interactive], rest)}
        >
            {Icon && (
                <div className={cn({ [styles.icon]: true, [styles.large]: !!subLabel })}>
                    <Icon size={subLabel ? 22 : 18} color={iconColor} />
                </div>
            )}
            <div className={cn({ [styles.box]: true, [styles.withSubLabel]: !!subLabel })}>
                <div className={styles.label} style={{ color: labelColor }}>
                    <div className={styles.title} style={{ color: labelColor }}>
                        {label}
                        {asterisksRestrictMode && warning ? ' *' : ''}
                    </div>
                    {subLabel && <div className={styles.subTitle}>{subLabel}</div>}
                </div>
                {preview && (
                    <div className={styles.preview} style={{ paddingRight: previewType === 'image' ? 0 : 6 }}>
                        {previewType === 'text' && preview}
                        {previewType === 'image' && (
                            <div className={styles.imageBox}>
                                <img src={preview} alt="preview" />
                            </div>
                        )}
                    </div>
                )}

                {rightNode}
            </div>
        </Tag>
    );
}

BlockListItemPreview.List = BlockListItemPreviewList;
BlockListItemPreview.ACLItem = BlockListItemPreviewWithACL(BlockListItemPreview);

BlockListItemPreview.propTypes = {
    tag: PropTypes.any,
    label: PropTypes.string.isRequired,
    subLabel: PropTypes.string,
    labelIcon: PropTypes.any.isRequired,
    rightIcon: PropTypes.any,
    previewType: PropTypes.oneOf(['text', 'image']),
    preview: PropTypes.oneOfType([PropTypes.string, PropTypes.element, PropTypes.number]),
    className: PropTypes.string,
    labelColor: PropTypes.string,
    iconColor: PropTypes.string,
    onClick: PropTypes.func,
    restrict: LandingsPropTypes.restriction,
    disabled: PropTypes.bool,
    noOnClick: PropTypes.bool,
    asterisksRestrictMode: PropTypes.bool,
};

BlockListItemPreview.defaultProps = {
    tag: 'div',
    previewType: 'text',
    labelColor: '#333',
    iconColor: '#333',
};
