import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import { FiHash, FiToggleRight, FiTrash } from 'react-icons/fi';
import { MdTitle } from 'react-icons/md';

import { useTemplate } from '@mssgme/hooks';
import { AclMenuPane } from '@mssgme/ui';

const menu = ({ t, template }) => [
    {
        to: 'status',
        label: t('Template.View.Settings.items.status'),
        icon: FiToggleRight,
        preview: template.public ? t('ui:labels.yes') : t('ui:labels.no'),
        rule: 'page-templates-publish',
    },
    {
        to: 'name',
        label: t('Template.View.Settings.items.name'),
        icon: MdTitle,
        preview: template.name,
    },
    {
        to: 'tags',
        label: t('Template.View.Settings.items.tags'),
        icon: FiHash,
        preview: [...new Set([...template.primaryTags, ...template.secondaryTags])].join(', '),
    },
    {
        to: 'delete',
        label: t('labels.template.delete'),
        labelColor: '#dc5960',
        icon: function DeleteItemIcon(iconProps) {
            return <FiTrash {...iconProps} color="#dc5960" />;
        },
    },
];

export function TemplateSettings() {
    const { t } = useTranslation('landings');
    const { templateId } = useParams();
    const template = useTemplate(templateId);

    const menuFactory = useCallback(() => menu({ t, template }), [t, template]);

    return <AclMenuPane title={t('Template.View.Settings.heading')} menuFactory={menuFactory} />;
}
