import React from 'react';
import Select from 'react-select';

import { isSameArray } from '@mssgme/helpers';
import { SelectFieldProps } from './types';
import FieldRow from './FieldRow';

const selectThemeOverride = (theme) => ({
    ...theme,
    colors: {
        ...theme.colors,
        primary: '#007aff',
        neutral20: '#c7c7cc',
        neutral30: '#8e8e93',
    },
});

const filterValue = (multiple, options, value) =>
    multiple
        ? (value && value.length && options.filter((item) => value.includes(item.value))) || []
        : options.find((item) => item.value === value);

export default function StyledSelectField({ onChange: parentChange, multiple, noOptionsMessage, ...props }) {
    return (
        <FieldRow {...props}>
            {({
                field: { name, value, onChange, disabled, ...rest },
                form: { setFieldValue, setFieldTouched },
                meta: { initialValue },
            }) => (
                <Select
                    theme={selectThemeOverride}
                    isMulti={!!multiple}
                    isClearable={false}
                    isSearchable={false}
                    isDisabled={disabled}
                    menuPlacement="auto"
                    name={name}
                    value={filterValue(multiple, rest.options, value)}
                    noOptionsMessage={noOptionsMessage}
                    onChange={(changed, { action, removedValue }) => {
                        switch (action) {
                            case 'remove-value':
                            case 'pop-value':
                                if (removedValue.isFixed) {
                                    return;
                                }

                                break;
                        }

                        const normalized = multiple
                            ? changed
                                ? changed.map((item) => item.value)
                                : []
                            : changed?.value || undefined;

                        const dirty = multiple
                            ? !isSameArray(initialValue, normalized, { ordered: true })
                            : normalized !== initialValue;

                        setFieldValue(name, dirty ? normalized : initialValue);
                        setFieldTouched(name, dirty, dirty);

                        parentChange && parentChange(name, normalized);
                    }}
                    {...rest}
                />
            )}
        </FieldRow>
    );
}

StyledSelectField.propTypes = SelectFieldProps;
