import PropTypes from 'prop-types';
import React, { useMemo } from 'react';

import { useNavigationUtils } from '@mssgme/hooks';
import { usePresenceRemoval } from '../../hooks';
import { ModalPane } from '../ModalEditPane';
import { BlockListItemPreview } from '../BlockListItemPreview';

export const AclMenuPane = ({ title, menuFactory, onClose }) => {
    const { generateUrl } = useNavigationUtils();
    const items = useMemo(() => menuFactory(), [menuFactory]);
    const closePath = onClose || generateUrl('..');

    usePresenceRemoval();

    return (
        <ModalPane title={title} onClose={closePath}>
            <BlockListItemPreview.List>
                {items
                    .filter((item) => item.filter !== false)
                    .map((item) => (
                        <BlockListItemPreview.ACLItem key={item.label} item={item} generateUrl={generateUrl} />
                    ))}
            </BlockListItemPreview.List>
        </ModalPane>
    );
};

AclMenuPane.propTypes = {
    title: PropTypes.any,
    menuFactory: PropTypes.func.isRequired,
    onClose: PropTypes.oneOfType([PropTypes.func, PropTypes.string]),
};
