import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { useLocalStorage } from 'react-use';
import { pick } from 'lodash';

import { getDefaultTheme } from '@mssgme/shared';
import { isMatchId } from '@mssgme/helpers';
import { useFetching } from './useFetching';
import { useActions } from './useActions';
import { useCollections } from './useCollections';
import { useWorkspace } from './useWorkspace';

export function useWebsites() {
    return useSelector((state) => state.websites);
}

export function useWebsite(predicate) {
    const website = useSelector((state) => state.websites.find(predicate));
    const specifier = useMemo(() => ({ website: website?._id }), [website?._id]);
    const { crm } = useWorkspace();
    const { items: collections } = useCollections(specifier);

    return useMemo(() => {
        if (!website) {
            return {
                homePageId: null,
                pages: [],
                collections,
                crm,
            };
        }

        return {
            ...website,
            crm: website.crm.onBoarding ? { ...crm, onBoarding: true } : website.crm,
            collections,
        };
    }, [crm, website, collections]);
}

export function useWebsiteById(id) {
    return useWebsite((item) => item._id === id);
}

export function useWebsiteBySlug(slug) {
    return useWebsite((item) => item._id === slug); //TODO: all routes replace website slug with id
}

export function clearWebsitePayload(website) {
    return pick(website, [
        'name',
        'slug',
        'seo',
        'tracking',
        'enabled',
        'branding',
        'embeddedCode',
        'scaffold',
        'theme',
        'localization',
    ]);
}

export function useWebsitePage(website, id = (website) => website.homePageId) {
    const pageId = typeof id === 'function' ? id(website) : id;
    const page = website.pages.find(isMatchId(pageId));

    return {
        website,
        page,
        pageId,
        websiteId: website._id,
    };
}

export const useWebsitesFactory = () => {
    const history = useHistory();
    const { api } = useActions();
    const [helpTipVisible, setHelpTipVisible] = useLocalStorage('visualBuilderHelpTip', true);

    const { pending, run, ...state } = useFetching(async ({ templateId, theme, redirect = true } = {}) => {
        const result = await api.websites.scaffold({
            payload: {
                theme: theme || getDefaultTheme(),
                templateId,
            },
        });

        if (redirect) {
            if (helpTipVisible) {
                setHelpTipVisible(false);
            }

            history.push({
                pathname: `/pages/-/${result._id}/edit${helpTipVisible ? '/help' : ''}`,
            });
        }
    }, []);

    return {
        pending,
        create: run, // `run` here would not change, as its dependencies are `[]`
        link: '/pages/create',
        ...state,
    };
};
