import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { cls } from '@mssgme/helpers';

import styles from './FloatingActions.scss';

export const FloatingActions = ({ children, gap = 32, padded, style: baseStyle, ...rest }) => {
    const style = useMemo(() => (padded ? baseStyle : { bottom: gap, margin: `${gap}px ${gap}px`, ...baseStyle }), [
        baseStyle,
    ]);

    return (
        <div style={style} {...cls([styles.root, padded && styles.padded], rest)}>
            {children}
        </div>
    );
};

FloatingActions.propTypes = {
    gap: PropTypes.number,
    style: PropTypes.object,
    padded: PropTypes.bool,
};
