import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { FiInstagram } from 'react-icons/fi';
import { SiTiktok } from 'react-icons/si';

import { useNavigationUtils } from '@mssgme/hooks';
import { ModalPane } from '@mssgme/ui';

import styles from './SocialsShareBio.scss';

export function SocialsShareBio({ backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const closePath = generateUrl(backPath);

    return (
        <ModalPane title={t('Editor.SocialsShareBio.title')} onClose={closePath}>
            <div>
                <Link
                    to={generateUrl('instagram')}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-instagram"
                >
                    <div
                        className={styles.button}
                        style={{
                            backgroundImage:
                                'linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d)',
                        }}
                    >
                        <div className={styles.icon}>
                            <FiInstagram size={20} />
                        </div>
                        <div className={styles.label}>{t('Editor.SocialsShareBio.title')} Instagram</div>
                    </div>
                </Link>
                <Link
                    to={generateUrl('tiktok')}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-tiktok"
                >
                    <div
                        className={styles.button}
                        style={{
                            backgroundImage: 'linear-gradient(45deg, #000, #666)',
                        }}
                    >
                        <div className={styles.icon}>
                            <SiTiktok size={20} />
                        </div>
                        <div className={styles.label}>{t('Editor.SocialsShareBio.title')} TikTok</div>
                    </div>
                </Link>
            </div>
        </ModalPane>
    );
}

SocialsShareBio.propTypes = {
    backPath: PropTypes.string,
};

SocialsShareBio.defaultProps = {
    backPath: '..',
};
