import { useMemo } from 'react';

import { checkRestrictionsOnAll, checkRestrictions } from '@mssgme/shared';
import { useWorkspace } from '@mssgme/hooks';

/**
 * @param {{ blocks: *[], theme: { colorId: string, styleId: string }}} page Page/template
 * @returns {{ upgrade: false|string, warning: { message: string, code: 'unpaid'|'forbidden'|'upgrade' } }} String ID of the plan to upgrade to or <i>false</i>
 */
export const usePageUpgradeCheck = (page) => {
    const workspace = useWorkspace();

    return useMemo(() => {
        if (!page) {
            return false;
        }

        let warning = false;
        const payload = { workspace };
        const callback = (error) => {
            warning = !!(error?.code === 'upgrade') && error.target ? error : false;
        };

        page.theme && checkRestrictions('page-themes:edit', payload, page.theme, callback);

        if (!warning && page.blocks.length) {
            checkRestrictionsOnAll('page-blocks-publish', payload, page.blocks, callback);
        }

        return {
            warning,
            upgrade: (warning && warning.target) || false,
        };
    }, [workspace, page?.blocks, page?.theme]);
};
