import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { TextField } from '@mssgme/ui';
import { WebsiteEditForm } from '../../../../UI';

export default function Url() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);

    return (
        <WebsiteEditForm title={t('Website.Edit.URL.heading')} website={website} backPath={generateUrl('..')}>
            {() => (
                <>
                    <TextField
                        builderInput
                        name="slug"
                        beforeInput={t('common:label.host')}
                        placeholder={t('Website.fields.slug.placeholder')}
                        maxLength={50}
                        autoCapitalize="off"
                        controlProps={{ style: { textTransform: 'lowercase' } }}
                    />
                </>
            )}
        </WebsiteEditForm>
    );
}
