import QRCode from 'qrcode';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useTimeoutFn } from 'react-use';

import { useNavigationUtils, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { Button, ModalPane, Spinner } from '@mssgme/ui';

import { useDomain } from '../../../../hooks';

import styles from './Qr.scss';

export function Qr({ backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);
    const { page } = useWebsitePage(website);
    const { isWaitingSSl, isReady } = useDomain(website);
    const [qrSrc, setQrSrc] = useState(null);
    const history = useHistory();
    const historyBackPath = history?.location?.state?.backPath;
    const websiteUrl =
        isWaitingSSl || isReady ? `http${isReady ? 's' : ''}://${website.domain.name}` : page.absoluteUrl;

    const closePath = generateUrl(historyBackPath || backPath);

    const generateQrSrc = async () => {
        const qrImageSrc = await QRCode.toDataURL(websiteUrl, {
            width: 1000,
        });

        setQrSrc(qrImageSrc);
    };

    useTimeoutFn(generateQrSrc, 1000);

    return (
        <ModalPane title={t('Editor.ShareQr.heading')} onClose={closePath}>
            {qrSrc && <p className={styles.tip}>{t('Editor.ShareQr.tip')}</p>}
            <div className={styles.qrWrapper}>
                {!qrSrc && (
                    <div className={styles.spinnerContainer}>
                        <Spinner />
                    </div>
                )}
                {qrSrc && <img className={styles.qr} src={qrSrc} alt="QR-code" />}
            </div>
            {qrSrc && (
                <Button className={styles.button} tag="a" href={qrSrc} download="my_simpl_qr.png" fullWidth>
                    {t('Editor.ShareQr.downloadQr')}
                </Button>
            )}
        </ModalPane>
    );
}

Qr.propTypes = {
    backPath: PropTypes.string,
};

Qr.defaultProps = {
    backPath: '..',
};
