import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { capitalize } from 'lodash';
import { useParams } from 'react-router-dom';
import { FiCopy, FiGlobe, FiLink, FiToggleRight, FiTrash } from 'react-icons/fi';
import { MdCode, MdTitle, MdTranslate } from 'react-icons/md';
import { AiOutlineBarChart } from 'react-icons/ai';

import config from 'config';
import { prettifyUrl } from '@mssgme/shared';
import { useFeatures, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { AclMenuPane } from '@mssgme/ui';
import { MssgmeLogoIcon } from '@mssgme/icons';

import { usePublish } from '../../../../UI';

import SeoIcon from '../../../../icons/seoIcon.svg';

// TODO: refactor, kiss
const menu = ({ t, page, website, canPublish, feature }) => {
    const activeAnalyticsPlatforms = Object.entries(website.tracking)
        .filter((entry) => !!entry[1])
        .map((entry) => capitalize(entry[0]));
    const filter = !website.scaffold;

    return [
        {
            to: !website.enabled && !page.blocks.length ? 'no-blocks' : 'status',
            label: t('Website.View.Settings.items.status'),
            icon: FiToggleRight,
            preview: website.enabled ? t('ui:labels.yes') : t('ui:labels.no'),
            filter: (!website.scaffold && canPublish) || website.enabled,
            'data-event': 'click-website-settings',
            'data-feature': 'status',
        },
        {
            to: 'name',
            label: t('Website.View.Settings.items.name'),
            icon: MdTitle,
            preview: website.name,
            filter,
            'data-event': 'click-website-settings',
            'data-feature': 'name',
        },
        {
            to: 'url',
            label: t('Website.View.Settings.items.slug'),
            icon: FiLink,
            preview: `${prettifyUrl(config.main_url)}/${website.slug}`,
            filter,
            'data-event': 'click-website-settings',
            'data-feature': 'url',
        },
        {
            to: 'domain',
            label: t('Website.View.Settings.items.domain'),
            icon: FiGlobe,
            restrictRule: 'domain',
            restrictAllowClick: true,
            preview: website.domain?.name || t('ui:labels.notSet'),
            filter,
            'data-event': 'click-website-settings',
            'data-feature': 'domain',
        },
        {
            to: 'locale',
            label: t('Website.View.Settings.items.locale'),
            icon: MdTranslate,
            preview: website.localization?.lang
                ? t(`Website.Edit.Locale.locales.${website.localization?.lang}`)
                : t('Website.Edit.Locale.locales.auto'),
            'data-event': 'click-website-settings',
            'data-feature': 'locale',
        },
        {
            to: 'seo',
            label: t('Website.View.Settings.items.seo'),
            icon: function SeoIconF() {
                return <SeoIcon style={{ display: 'block', width: 18, height: 18, color: '#333' }} />;
            },
            restrictRule: 'page-seo:edit',
            restrictAllowClick: true,
            preview: website.seo.title || website.seo.description ? t('labels.changed') : t('labels.asDefault'),
            'data-event': 'click-website-settings',
            'data-feature': 'seo',
        },
        {
            to: 'tracking',
            label: t('Website.View.Settings.items.tracking'),
            icon: AiOutlineBarChart,
            restrictRule: 'page-tracking:edit',
            restrictAllowClick: true,
            preview: activeAnalyticsPlatforms.length > 0 ? activeAnalyticsPlatforms.join(', ') : t('ui:labels.notSet'),
            'data-event': 'click-website-settings',
            'data-feature': 'tracking',
        },
        {
            to: 'embedded',
            label: t('Website.View.Settings.items.embedded'),
            icon: MdCode,
            preview: website.embeddedCode?.content,
            restrictRule: 'page-embedding:edit',
            restrictAllowClick: true,
            'data-event': 'click-website-settings',
            'data-feature': 'embedded',
        },
        {
            to: 'branding',
            label: t('Website.View.Settings.items.branding'),
            icon: function BrandingItemIcon(iconProps) {
                return (
                    <MssgmeLogoIcon
                        {...iconProps}
                        style={{ display: 'block', width: 16, position: 'relative', left: 1 }}
                    />
                );
            },
            restrictRule: 'page-branding:edit',
            preview: website.branding ? t('ui:labels.on') : t('ui:labels.off'),
            'data-event': 'click-website-settings',
            'data-feature': 'branding',
        },
        {
            to: 'template',
            label: t('Website.View.Settings.items.template'),
            icon: function BrandingItemIcon(iconProps) {
                return <FiCopy {...iconProps} style={{ display: 'block', width: 19 }} />;
            },
            restrictRule: 'page-templates:create',
            filter: !!(page.blocks.length && feature('templates')),
            'data-event': 'click-website-settings',
            'data-feature': 'template',
        },
        {
            to: 'delete',
            label: t('labels.website.delete'),
            labelColor: '#dc5960',
            icon: function DeleteItemIcon(iconProps) {
                return <FiTrash {...iconProps} color="#dc5960" />;
            },
            'data-event': 'click-website-settings',
            'data-feature': 'delete',
        },
    ];
};

export function WebsiteSettings() {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { canPublish } = usePublish(website, page);
    const feature = useFeatures();
    const menuFactory = useCallback(() => menu({ t, website, page, canPublish, feature }), [
        t,
        website,
        page,
        canPublish,
        feature,
    ]);

    return <AclMenuPane title={t('Website.View.Settings.heading')} menuFactory={menuFactory} />;
}
