import { useCallback, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { useFetching } from '@mssgme/hooks';
import { NotificationCenter } from '@mssgme/ui';

export const usePromptAction = (handler, onDone) => {
    const { t } = useTranslation('landings');
    const [context, setContext] = useState(null);
    const { pending, run } = useFetching(
        async (context) => {
            try {
                const result = await handler(context);

                onDone && onDone(context, result);
            } catch (e) {
                NotificationCenter.getInstance().danger(t(e.message), { showClose: true });
            }
        },
        [handler, onDone]
    );

    const handle = useCallback(
        async (confirmed) => {
            setContext(null);

            if (!confirmed) {
                return;
            }

            return run(context);
        },
        [context, onDone]
    );

    return {
        pending,
        isShown: !!context,
        initiate: setContext,
        run,
        handle,
    };
};
