import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { List } from '@mssgme/userpage-core';
import {
    BlockEditForm,
    EditStylingForm,
    ListBlockSchema,
    StyledBlockPreview,
    useBlockEditor,
} from '../../../components';
import { ListItemsField } from './ListItemsField';

export function ListEdit({ blockId }) {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { bind } = useBlockEditor({ website, page, blockId, as: ListEdit });

    return (
        <BlockEditForm {...bind} validationSchema={ListBlockSchema} title={t('Page.Edit.List.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <ListItemsField name="items" />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

ListEdit.propTypes = {
    blockId: PropTypes.string,
};

ListEdit.block = List;
