import { useMemo } from 'react';

import { useCollections } from '@mssgme/hooks';

export const useImportableCollections = (specifier) => {
    const excludes = useMemo(() => ({ exclude: specifier }), [specifier]);
    const collections = useCollections(excludes);
    const filtered = useMemo(() => collections.items.filter((col) => col.productsCount), [collections.items]);

    return { items: filtered, excludes };
};
