import PropTypes from 'prop-types';
import React from 'react';

import { cls } from '@mssgme/helpers';

import styles from './StreamlinedScreen.scss';

export const Section = ({ nopadding, top, ...props }) => {
    return <div {...cls([styles.section, nopadding && styles.noPadding, top && styles.top], props)} />;
};

Section.propTypes = {
    nopadding: PropTypes.bool,
    top: PropTypes.bool,
};
