import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useRouteMatch } from 'react-router-dom';

import { EyeIcon, DropletIcon } from '@mssgme/icons';

import { DashboardActionButton } from './DashboardActionButton';

import styles from './Dashboard.scss';

DefaultDashboardActions.propTypes = {
    isEmptyPage: PropTypes.bool.isRequired,
};

export function DefaultDashboardActions({ isEmptyPage }) {
    const { t } = useTranslation('landings');
    const { url } = useRouteMatch();

    return (
        <div className={styles.row}>
            <DashboardActionButton
                to={`${url}/styling`}
                title={t('Editor.Dashboard.items.theme')}
                icon={DropletIcon}
                disabled={isEmptyPage}
            />
            <DashboardActionButton
                to={`${url}/preview/mobile`}
                title={t('Editor.Dashboard.items.preview')}
                icon={EyeIcon}
                disabled={isEmptyPage}
            />
        </div>
    );
}
