import PropTypes from 'prop-types';
import React, { useCallback, useRef } from 'react';
import { useTranslation } from 'react-i18next';

import { useActions } from '@mssgme/hooks';
import { normalizeRawUrl } from '@mssgme/helpers';
import { ImageField, ImagePreview, TextField, ToggleField } from '@mssgme/ui';
import { MediaSizes, useUploads } from '@mssgme/userpage-core';

import { ArrayField, ImageGalleryItemSchema, useWebsiteEditor } from '../../../components';

const ImageGalleryItem = ({ value, className, textClassName }) => {
    const { t } = useTranslation('landings');
    const annotation = value.annotation || value.externalUrl;

    return (
        <div className={className} title={annotation}>
            <ImagePreview url={value.absoluteUrl} />

            <div className={textClassName}>
                {' '}
                {annotation ? annotation.replace(/\n/g, ' ') : t('Page.Edit.ImageGallery.fields.annotation.missing')}
            </div>
        </div>
    );
};

ImageGalleryItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const ImageGalleryItemsField = (props) => {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { getMediaSizeUrl } = useUploads();
    const { websiteId } = useWebsiteEditor();
    const { current: labels } = useRef({
        addButton: {
            more: t('Page.Edit.ImageGallery.itemAddMore'),
            first: t('Page.Edit.ImageGallery.itemAdd'),
        },
        modal: {
            title: t('Page.Edit.ImageGallery.itemFormHeading'),
        },
    });
    const itemFactory = useCallback(() => ({
        url: '',
        absoluteUrl: '',
        externalUrl: '',
        modal: true,
        annotation: '',
    }));

    return (
        <ArrayField
            factory={itemFactory}
            labels={labels}
            Item={ImageGalleryItem}
            validationSchema={ImageGalleryItemSchema}
            {...props}
        >
            {({ values, setFieldValue }) => (
                <div>
                    <ImageField
                        required
                        name="url"
                        innerLabel={t('Page.Edit.ImageGallery.fields.url.label')}
                        previewUrl={getMediaSizeUrl(values.absoluteUrl, MediaSizes.Icon)}
                        upload={(payload) => api.websites.upload({ ...payload, websiteId })}
                        onChange={({ path, url }) => {
                            setFieldValue('url', path || '');
                            setFieldValue('absoluteUrl', url || '');
                        }}
                    />
                    <TextField
                        builderInput
                        name="externalUrl"
                        label={t('Page.Edit.Image.fields.externalUrl.label')}
                        placeholder={t('Page.Edit.Image.fields.externalUrl.placeholder')}
                        maxLength={2000}
                        clearable
                        onBlur={() => {
                            const url = normalizeRawUrl(values.externalUrl || '');

                            if (url && url !== values.externalUrl) {
                                setFieldValue('externalUrl', url);
                            }
                        }}
                    />
                    <ToggleField
                        builderInput
                        label={t('Page.Edit.Image.fields.modal.label')}
                        name="modal"
                        value={values.externalUrl ? false : !!values.modal}
                        disabled={!!values.externalUrl}
                    />

                    {/*<TextField*/}
                    {/*    builderInput*/}
                    {/*    name="annotation"*/}
                    {/*    label={t('Page.Edit.ImageGallery.fields.annotation.label')}*/}
                    {/*    placeholder={t('Page.Edit.ImageGallery.fields.annotation.placeholder')}*/}
                    {/*    maxLength={50}*/}
                    {/*/>*/}
                </div>
            )}
        </ArrayField>
    );
};

ImageGalleryItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
