import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { IoMdColorPalette } from 'react-icons/io';
import { MdStyle } from 'react-icons/md';
import { FiImage } from 'react-icons/fi';

import { AclMenuPane } from '@mssgme/ui';

const restrict = (item) => ({ rule: 'page-styling', subject: item, iconOnly: true });

const menu = ({ t }) => [
    {
        to: 'theme',
        label: t('Editor.PageStyling.items.theme'),
        icon: MdStyle,
    },
    {
        to: 'background-color',
        type: 'backgroundColor',
        label: t('Editor.PageStyling.items.backgroundColor'),
        icon: IoMdColorPalette,
        restrict,
    },
    {
        to: 'background-image',
        type: 'backgroundImage',
        label: t('Editor.PageStyling.items.backgroundImage'),
        icon: FiImage,
        restrict,
    },
    // {
    //     to: 'alignment',
    //     label: t('Editor.PageStyling.items.alignment'),
    //     icon: MdVerticalAlignCenter,
    // },
];

export function PageStyling() {
    const { t } = useTranslation('landings');
    const menuFactory = useCallback(() => menu({ t }), [t]);

    return <AclMenuPane title={t('Editor.PageStyling.heading')} menuFactory={menuFactory} />;
}
