import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import { Link, useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { TextField } from '@mssgme/ui';
import { useNavigationUtils, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { WebsiteEditForm } from '../../../../UI';

import styles from './EditWebsiteName.scss';

export function EditWebsiteName({ backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { website } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const domainName = website.domain?.name;
    const closePath = generateUrl(backPath);

    return (
        <WebsiteEditForm
            website={website}
            title={t('Editor.EditWebsiteName.title')}
            backPath={closePath}
            successRedirectPath={closePath}
            saveButtonTitle={t('Editor.EditWebsiteName.save')}
            noGuardChanges
            initialEnabledSubmitButton
        >
            {() => (
                <>
                    <TextField
                        builderInput
                        name="slug"
                        beforeInput={t('common:label.host')}
                        label={t('Website.fields.slug.publishLabel')}
                        placeholder={t('Website.fields.slug.placeholder')}
                        maxLength={50}
                        autoCapitalize="off"
                        controlProps={{ style: { textTransform: 'lowercase' } }}
                    />
                    <div className={styles.domainPreview}>
                        <span className={styles.label}>{t('Editor.EditWebsiteName.domain.label')}</span>
                        <div className={styles.preview}>
                            <div className={styles.value}>
                                <span className={cn({ [styles.empty]: !domainName })}>
                                    {domainName || 'www.example.com'}
                                </span>
                            </div>
                            <div className={styles.after}>
                                <Link to={generateUrl('domain')}>
                                    {domainName
                                        ? t('Editor.EditWebsiteName.domain.buttons.change')
                                        : t('Editor.EditWebsiteName.domain.buttons.connect')}
                                </Link>
                            </div>
                        </div>
                    </div>
                </>
            )}
        </WebsiteEditForm>
    );
}

EditWebsiteName.propTypes = {
    backPath: PropTypes.string,
};

EditWebsiteName.defaultProps = {
    backPath: '..',
};
