import React from 'react';
import PropTypes from 'prop-types';
import { Link, useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { FiInstagram, FiFacebook, FiTwitter } from 'react-icons/fi';
import { FaVk, FaFacebookMessenger, FaWhatsapp, FaTelegramPlane, FaViber } from 'react-icons/fa';
import { SiTiktok } from 'react-icons/si';
import {
    FacebookShareButton,
    VKShareButton,
    TwitterShareButton,
    FacebookMessengerShareButton,
    WhatsappShareButton,
    TelegramShareButton,
    ViberShareButton,
} from 'react-share';

import config from 'config';
import { useNavigationUtils, useWebsitePage, useWebsiteBySlug } from '@mssgme/hooks';
import { ModalPane } from '@mssgme/ui';

import { useDomain } from '../../../../hooks';

import styles from './SocialsShare.scss';

export function SocialsShare({ backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);
    const { isWaitingSSl, isReady } = useDomain(website);
    const { page } = useWebsitePage(website);
    const websiteUrl =
        isWaitingSSl || isReady ? `http${isReady ? 's' : ''}://${website.domain.name}` : page.absoluteUrl;
    const closePath = generateUrl(backPath);

    return (
        <ModalPane title={t('Editor.ShareSocial.heading')} onClose={closePath}>
            <div>
                <Link
                    to={generateUrl('instagram')}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-instagram"
                >
                    <div
                        className={styles.button}
                        style={{
                            backgroundImage:
                                'linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d)',
                        }}
                    >
                        <div className={styles.icon}>
                            <FiInstagram size={20} />
                        </div>
                        <div className={styles.label}>Instagram</div>
                    </div>
                </Link>
                <Link
                    to={generateUrl('tiktok')}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-tiktok"
                >
                    <div
                        className={styles.button}
                        style={{
                            backgroundImage: 'linear-gradient(45deg, #000, #666)',
                        }}
                    >
                        <div className={styles.icon}>
                            <SiTiktok size={20} />
                        </div>
                        <div className={styles.label}>TikTok</div>
                    </div>
                </Link>
                <FacebookShareButton
                    url={websiteUrl}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-facebook"
                >
                    <div className={styles.button} style={{ backgroundColor: 'rgb(59, 89, 152)' }}>
                        <div className={styles.icon}>
                            <FiFacebook size={20} />
                        </div>
                        <div className={styles.label}>Facebook</div>
                    </div>
                </FacebookShareButton>
                {/*<VKShareButton*/}
                {/*    url={websiteUrl}*/}
                {/*    className={styles.buttonContainer}*/}
                {/*    data-button="btn-share-socials-vkontakte"*/}
                {/*>*/}
                {/*    <div className={styles.button} style={{ backgroundColor: 'rgb(69, 102, 142)' }}>*/}
                {/*        <div className={styles.icon}>*/}
                {/*            <FaVk size={19} />*/}
                {/*        </div>*/}
                {/*        <div className={styles.label}>Vkontakte</div>*/}
                {/*    </div>*/}
                {/*</VKShareButton>*/}
                <TwitterShareButton
                    url={websiteUrl}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-twitter"
                >
                    <div className={styles.button} style={{ backgroundColor: '#00aced' }}>
                        <div className={styles.icon}>
                            <FiTwitter size={19} />
                        </div>
                        <div className={styles.label}>Twitter</div>
                    </div>
                </TwitterShareButton>
                <WhatsappShareButton
                    url={websiteUrl}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-whatsapp"
                >
                    <div className={styles.button} style={{ backgroundColor: '#25D366' }}>
                        <div className={styles.icon}>
                            <FaWhatsapp size={20} />
                        </div>
                        <div className={styles.label}>WhatsApp</div>
                    </div>
                </WhatsappShareButton>
                <ViberShareButton
                    url={websiteUrl}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-viber"
                >
                    <div className={styles.button} style={{ backgroundColor: '#7360f2' }}>
                        <div className={styles.icon}>
                            <FaViber size={20} />
                        </div>
                        <div className={styles.label}>Viber</div>
                    </div>
                </ViberShareButton>
                <FacebookMessengerShareButton
                    appId={config.facebook_app_id}
                    url={websiteUrl}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-messenger"
                >
                    <div className={styles.button} style={{ backgroundColor: '#0078FF' }}>
                        <div className={styles.icon}>
                            <FaFacebookMessenger size={20} />
                        </div>
                        <div className={styles.label}>Messenger</div>
                    </div>
                </FacebookMessengerShareButton>
                <TelegramShareButton
                    url={websiteUrl}
                    className={styles.buttonContainer}
                    data-button="btn-share-socials-telegram"
                >
                    <div className={styles.button} style={{ backgroundColor: '#0088cc' }}>
                        <div className={styles.icon}>
                            <FaTelegramPlane size={20} />
                        </div>
                        <div className={styles.label}>Telegram</div>
                    </div>
                </TelegramShareButton>
            </div>
        </ModalPane>
    );
}

SocialsShare.propTypes = {
    backPath: PropTypes.string,
};

SocialsShare.defaultProps = {
    backPath: '..',
};
