import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';

import styles from './ListNoStyle.scss';

export default function ListNoStyle({ children, className, gutter, ...rest }) {
    children = React.Children.toArray(children);

    return (
        <ul className={cn({ [styles.root]: true, [className]: !!className })} {...rest}>
            {children.map((item, index) => (
                <li key={index} style={{ marginBottom: children[index + 1] ? gutter : 0 }}>
                    {item}
                </li>
            ))}
        </ul>
    );
}

ListNoStyle.propTypes = {
    className: PropTypes.string,
    children: PropTypes.any.isRequired,
    gutter: PropTypes.number,
};

ListNoStyle.defaultProps = {
    className: '',
    gutter: 16,
};
