import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { matchPath, Redirect, Route, Switch, useLocation } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';

import { useBackProductsPath, useProductsContext } from '../hooks';
import {
    CollectionEdit,
    CollectionProducts,
    Collections,
    PaymentProviderSettingsScreen,
    ProductEdit,
    ProductsNotifications,
    ProductsContacts,
    ProductsCurrency,
    ProductsHome,
    ProductsImport,
    ProductsOnboarding,
    ProductsOrderForm,
} from './Editing';
import { Payments, ProductsSettingsMenu } from './SettingsMenu';

const routesFactory = (path) => [
    { path: `${path}`, component: ProductsHome },
    { path: `${path}/settings`, component: ProductsSettingsMenu },
    { path: `${path}/settings/currency`, component: ProductsCurrency },
    { path: `${path}/settings/messengers`, component: ProductsContacts },
    { path: `${path}/settings/notifications`, component: ProductsNotifications },
    { path: `${path}/settings/form`, component: ProductsOrderForm },
    { path: `${path}/settings/onboard/:fromStart?`, component: ProductsOnboarding },
    { path: `${path}/settings/payments`, component: Payments },
    { path: `${path}/settings/payments/:provider`, component: PaymentProviderSettingsScreen },
    { path: `${path}/settings/import`, component: ProductsImport },
    { path: `${path}/collections`, component: Collections },
    { path: `${path}/collections/new`, component: CollectionEdit },
    { path: `${path}/collections/:collectionId([a-f\\d]{24})`, component: CollectionProducts },
    { path: `${path}/collections/:collectionId([a-f\\d]{24})/new`, component: ProductEdit },
    { path: `${path}/collections/:collectionId([a-f\\d]{24})/product/edit/:productId`, component: ProductEdit },
    { path: `${path}/collections/:collectionId/edit`, component: CollectionEdit },
    { path: `${path}/new/:collectionId?`, component: ProductEdit },
    { path: `${path}/edit/:productId`, component: ProductEdit },
];

export function ProductsPage() {
    const { router, crm, modal } = useProductsContext();
    const { pathname, state } = useLocation();
    const backUrl = useMemo(() => state?.blockUrl, []);
    const routes = useMemo(() => routesFactory(router.toHome()), [router.toHome()]);
    const isOnboarding = !!matchPath(pathname, router.settings.toOnboarding());

    const match = ({ component: Component, props, ...to }) => {
        const matched = matchPath(pathname, to);

        return matched && <Component key={to.path} {...matched.params} {...props} />;
    };

    useBackProductsPath(backUrl);

    if (crm.onBoarding && !isOnboarding) {
        return <Redirect to={router.settings.toOnboarding()} />;
    }

    if (modal) {
        return <AnimatePresence>{routes.map(match)}</AnimatePresence>;
    }

    return (
        <Switch>
            {routes.map(({ component: Tag, props, ...to }) => (
                <Route key={to.path} exact render={({ match: { params } }) => <Tag {...props} {...params} />} {...to} />
            ))}
        </Switch>
    );
}

ProductsPage.propTypes = {
    defaultMessengers: PropTypes.any,
};
