import { useEffect } from 'react';

export const useElementStyle = (node, style) => {
    useEffect(() => {
        if (!style) {
            return;
        }

        const prev = {};

        for (const key in style) {
            prev[key] = node.style[key];
        }

        Object.assign(node.style, style);

        return () => {
            Object.assign(node.style, prev);
        };
    }, [!!style]);
};
