import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';

import { normalizeRawUrl } from '@mssgme/helpers';
import { useFeature } from '@mssgme/hooks';
import { LandingsPropTypes } from '../../../domainTypes';
import SelectPageField from '../SelectPageField';
import TextField from '../TextField';
import TextDivider from './TextDivider';
import AutosuggestStyle from './AutosuggestStyle';

import styles from './SmartLinkField.scss';

export default function SmartLinkField({ name, website, themeFields, required, urlLabel, titleLabel, ...rest }) {
    const { t } = useTranslation('ui');
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { value } = getFieldMeta(name);
    const applySuggestions = useFeature('link_auto_branding');
    const showPageSelector = website?.pages.length > 1;
    const hasPage = showPageSelector && !!value.page;

    const handleNormalizeUrl = useCallback(() => {
        const url = normalizeRawUrl(value.url || '');

        if (url && url !== value.url) {
            setFieldValue(`${name}.url`, url);
        }
    }, [name, value.url, setFieldValue]);

    return (
        <div {...rest}>
            <TextField
                builderInput
                name={`${name}.title`}
                label={titleLabel || t('SmartLinkField.title.label')}
                placeholder={t('SmartLinkField.title.placeholder')}
                disabled={hasPage}
            />

            <TextField
                builderInput
                autoCapitalize="off"
                style={{ marginBottom: 16 }}
                required={required && !showPageSelector}
                name={`${name}.url`}
                label={urlLabel || t('SmartLinkField.url.label')}
                placeholder={t('SmartLinkField.url.placeholder')}
                disabled={hasPage}
                onBlur={handleNormalizeUrl}
            />

            {applySuggestions && themeFields && <AutosuggestStyle link={value} themeFields={themeFields} />}

            {showPageSelector && (
                <>
                    <TextDivider>{t('labels.or')}</TextDivider>
                    <SelectPageField className={styles.smaller} website={website} name={`${name}.page`} />
                </>
            )}
        </div>
    );
}

SmartLinkField.propTypes = {
    name: PropTypes.string.isRequired,
    required: PropTypes.bool,
    website: LandingsPropTypes.website,
    urlLabel: LandingsPropTypes.inputLabel,
    titleLabel: LandingsPropTypes.inputLabel,
    themeFields: LandingsPropTypes.linkThemeFields,
};
