import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { cls } from '@mssgme/helpers';
import { useRestriction } from '@mssgme/hooks';
import { Button, PlanBadge } from '@mssgme/ui';

import styles from './styles.scss';

export const ArrayItemAddButton = ({ name, label, restriction, items, onClick, ...rest }) => {
    const { t } = useTranslation('ui');
    const { warning } = useRestriction(restriction, { [name]: items });

    const addLabel = (
        label
            ? typeof label === 'string'
                ? label
                : items.length >= 1
                    ? label.more
                    : label.first
            : t('labels.add')
    );

    return (
        <Button
            tag="a"
            fullWidth
            iconFixed="right"
            afterIcon={warning?.target && <PlanBadge inverse plan={warning.target} />}
            onClick={onClick}
            {...cls(styles.addButton, rest)}
        >
            +{' '}{addLabel}
        </Button>
    );
};

ArrayItemAddButton.propTypes = {
    name: PropTypes.string.isRequired,
    items: PropTypes.array.isRequired,
    label: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.shape({ first: PropTypes.string.isRequired, more: PropTypes.string.isRequired }),
    ]),
    restriction: PropTypes.string,
    onClick: PropTypes.func.isRequired,
};
