import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { TextField } from '@mssgme/ui';
import { WebsiteEditForm } from '../../../../UI';

export default function Name() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);

    return (
        <WebsiteEditForm title={t('Website.Edit.Name.heading')} website={website} backPath={generateUrl('..')}>
            {() => (
                <>
                    <TextField
                        builderInput
                        name="name"
                        placeholder={t('Website.fields.name.placeholder')}
                        maxLength={250}
                    />
                </>
            )}
        </WebsiteEditForm>
    );
}
