import React from 'react';
import cn from 'classnames';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { TextField, Button, FieldTip } from '@mssgme/ui';
import { DomainEditForm } from '../../../../UI';
import { useDomain } from '../../../../hooks';

import styles from './Domain.scss';

export default function Domain({ ...props }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);
    const domainName = website?.domain?.name;
    const { isNew, isReady, isWaitingSSl, DNSRecords } = useDomain(website);
    const websiteUrl = isWaitingSSl || isReady ? `http${isReady ? 's' : ''}://${domainName}` : '';

    return (
        <DomainEditForm
            title={t('Website.Edit.Domain.heading')}
            website={website}
            backPath={generateUrl('..')}
            saveButtonTitle={t('Website.Edit.Domain.buttons.connect')}
            {...props}
        >
            {({ isSubmitting, handleDelete, isDeleting }) => (
                <>
                    {!domainName && (
                        <FieldTip>
                            <p>{t('Website.Edit.Domain.tips.empty.messages.1')}</p>
                            <p
                                style={{ margin: 0 }}
                                dangerouslySetInnerHTML={{
                                    __html: t('Website.Edit.Domain.tips.empty.messages.2'),
                                }}
                            />
                        </FieldTip>
                    )}
                    {isNew && domainName && (
                        <div>
                            <FieldTip title={t('Website.Edit.Domain.tips.new.title')} className={styles.fieldTip}>
                                <p>{t('Website.Edit.Domain.tips.new.messages.1')}</p>
                                <ol className={styles.list}>
                                    <li style={{ marginBottom: 6 }}>
                                        {t('Website.Edit.Domain.tips.new.messages.list.1')}
                                    </li>
                                    <li style={{ marginBottom: 6 }}>
                                        {t('Website.Edit.Domain.tips.new.messages.list.2')}
                                    </li>
                                    <li
                                        style={{ marginBottom: 6 }}
                                        dangerouslySetInnerHTML={{
                                            __html: t('Website.Edit.Domain.tips.new.messages.list.3', {
                                                host: DNSRecords?.[1]?.host,
                                                cname: DNSRecords?.[1]?.value,
                                            }),
                                        }}
                                    />
                                    <li
                                        dangerouslySetInnerHTML={{
                                            __html: t('Website.Edit.Domain.tips.new.messages.list.4', {
                                                host: DNSRecords?.[1]?.host,
                                            }),
                                        }}
                                    />
                                </ol>
                                <p style={{ margin: 0 }}>{t('Website.Edit.Domain.tips.new.messages.ps')}</p>
                                <p style={{ margin: '12px 0 0 0' }}>
                                    <a href="https://mssg.me/faq-next#q3-3" target="_blank" rel="noopener noreferrer">
                                        {t('Website.Edit.Domain.tips.new.messages.faq')}
                                    </a>
                                </p>
                            </FieldTip>
                        </div>
                    )}
                    {isWaitingSSl && (
                        <FieldTip title={t('Website.Edit.Domain.tips.waitingSSL.title')}>
                            {t('Website.Edit.Domain.tips.waitingSSL.messages.1')}
                        </FieldTip>
                    )}
                    {isReady && (
                        <FieldTip title={t('Website.Edit.Domain.tips.ready.title')}>
                            {t('Website.Edit.Domain.tips.ready.messages.1')}
                        </FieldTip>
                    )}
                    {!domainName && (
                        <TextField
                            label={t('Website.Edit.Domain.form.domainName.label')}
                            style={{ marginTop: 16 }}
                            builderInput
                            name="name"
                            placeholder={t('Website.fields.domain.name.placeholder')}
                            maxLength={50}
                            autoCapitalize="off"
                            disabled={isNew || isWaitingSSl}
                            // restrictRule="domain:edit"
                        />
                    )}
                    {domainName && (
                        <>
                            <div className={styles.statusContainer}>
                                <span className={styles.label}>{t('Website.Edit.Domain.form.domainName.label')}</span>
                                <div className={styles.status}>
                                    <div className={styles.domain}>
                                        {websiteUrl && (
                                            <a
                                                href={websiteUrl} //TODO: change http to https when ssl generating feature will be added
                                                target="_blank"
                                                rel="noopener noreferrer"
                                            >
                                                {domainName}
                                            </a>
                                        )}
                                        {!websiteUrl && <span>{domainName}</span>}
                                    </div>
                                    <div className={styles.statusMark}>
                                        <div
                                            className={cn({
                                                [styles.mark]: true,
                                                [styles.waitingSSL]: isWaitingSSl,
                                                [styles.ready]: isReady,
                                            })}
                                            title={
                                                isNew
                                                    ? 'Resolving domain...'
                                                    : isReady
                                                        ? 'Domain connected'
                                                        : isWaitingSSl
                                                            ? 'Generating SSL...'
                                                            : null
                                            }
                                        />
                                    </div>
                                </div>
                            </div>
                            <Button
                                fullWidth
                                tag="div"
                                btnStyle="danger"
                                isFetching={isDeleting || isSubmitting}
                                onClick={handleDelete}
                                style={{ marginTop: 16 }}
                            >
                                {t('Website.Edit.Domain.buttons.remove')}
                            </Button>
                        </>
                    )}
                </>
            )}
        </DomainEditForm>
    );
}
